<template>
    <main>
      <secondary-header-component :class="{ 'slide-top': loading }"
        >Certifications</secondary-header-component
      >
      <secondary-sub-component :class="{ 'slide-top': loading }"
        >Here are some of the certifications that I have earned.</secondary-sub-component
      >
      <certifications-component></certifications-component>
    </main>
  </template>
  
  <script>
  import SecondaryHeaderComponent from "../components/SecondaryHeaderComponent";
import SecondarySubComponent from "../components/SecondarySubComponent";
import CertificationsComponent from "../components/CertificationsComponent";

export default {
  components: {
    SecondaryHeaderComponent,
    SecondarySubComponent,
    CertificationsComponent,
  },
  data() {
    return {
      loading: true,
    };
  },

  mounted() {
    setTimeout(() => {
      this.loading = false;
    }, 200);
  },
};
</script>

<style lang="scss">
.slide-top {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-5rem);
}
</style>
